@use '@angular/material' as mat;


html {
  color-scheme: dark;
  @include mat.theme((
    color: mat.$cyan-palette,
    typography: (
      plain-family: Roboto,
      bold-weight: 500,
      medium-weight: 300,
      regular-weight: 100,
    ),
    density: -1
  ));
}


.help-label {
  font-size: x-small;
  line-height: 80%;
  display: inline-block;
  text-align: left;
  margin: 5px;
  padding: 5px;
}

a:link { text-decoration: none;color: gray;  }
a:visited { text-decoration: none;color: gray;  }
a:hover { text-decoration: none; color: lightgray; }
a:active { text-decoration: none; color: gray; }


.icon-button {
  border-radius: 40%;
  cursor:pointer;
  margin:4px;
  padding: 6px;
  margin-bottom:-14px;
  background-color: #121212;
  font-size:x-large;
  color: #23aca7;
}

.small-icon-button {
  border-radius: 40%;
  cursor:pointer;
  margin:2px;
  margin-bottom:-2px;
  background-color: #121212;
  font-size:x-large;
  color: #23aca7;
}


.mat-slide-label {
  font-size: x-small;
  line-height: 90%;
  text-align: left;
}



.map_style {
  display: inline-block;
  position:relative;
  z-index: 0;
  width:80vw;
  height:80vw;
  max-width: 600px;
  max-height: 60vh;
  pointer-events: none;
  overflow: hidden;
  margin: 15px;
}


.vignette {
  position: relative;
  max-width: 250px;
  max-height: 250px;
  width: 50%;
  cursor: pointer;
  margin: 10px;
}

.small-vignette {
  position: relative;
  width: 75px;
  height: 75px;
  overflow: hidden;
  object-fit: cover;
  cursor: pointer;
  margin: 5px;
}

.panel {
  margin: 10px;
}

.blocbutton {
  line-height: 85%;
}


.greylabel {
  font-size: small;
  opacity: 0.5;
  line-height: 85%;
  font-weight: lighter;
}

.data-table {
  display: inline-block;
  line-height: 90%;
  font-size: small;
  border-spacing: 5px;
  margin: 10px;
  text-align: left;
}

.mainform {
  padding: 0.2%;
  background-color: black;
  background-size: cover;
  text-align: center;
  width:100vw;
  overflow: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.vignette-zone {
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
  justify-items: center;
  width:100%;
}

.disk {
  background-color: #535353; /* Set the color of the disk */
  opacity: 0.2;
  border-radius: 50%; /* Make it a circle */
  z-index: 100;
  left:50%;
  top: 50%;
  /*transform: translate(30px,37px);*/
  position:absolute;
  display: block;
}

.smalltext {
  font-size: small;
  font-weight: lighter;
}
